.mainsection {
  width: 100%;
}

.mainsection > :nth-child(1) {
  margin-bottom: 2em;
}

.vertical-timeline-element-subtitle {
  margin-top: 0.5em;
  font-size: 1.6em;
}

.vertical-timeline-element-title {
  font-size: 1.4em;
  color: teal;
}

#Course{
  color: green;
}
.TimeLine{
  /* width: 100%; */
  height: 650px;
  display: grid;
  /* border: solid red 1px; */
  grid-template-columns: 45% 45%;
  /* grid-template-rows: 50% 50%; */
  gap: 8%;
  justify-content: space-around;
  align-items: center;
}

.TimeLine>div{
  /* width: 47%; */
  display: flex;
  border: solid rgb(236, 240, 236) 1px;
  box-shadow: rgba(239, 234, 234, 0.36) 0px 10px 36px 0px;
  border-radius: 15px;
  justify-content: space-around;
  align-items: center;
  
}

.box{
  display: flex;
  flex-direction: column;
  /* border: solid pink 1px; */
  align-items: center;
  justify-content: space-around;
  gap: 1rem;
  padding: 5px;
  border-radius: 15px;
 }

.box>div{
 display: flex;
 flex-direction: column;
 /* border: solid pink 1px; */
 
}

#icon{
  display: flex;
  flex-direction: row;
  gap: 5px;
}

#icon>div{
  display: flex;
  flex-direction: row;
  /* border: solid red 1px; */
  
}
#iconSize{
  font-size: 5rem;
}

@media (max-width: 900px) {
  .TimeLine{
    height: 720px;
  }

  .vertical-timeline-element-subtitle {
    margin-top: 0.5em;
    font-size: 1.2em;
  }
  
  .vertical-timeline-element-title {
    font-size: 1.0em;
    color: teal;
  }
}

@media (max-width: 400px) {
  .TimeLine{
    height: 800px;
  }

  .vertical-timeline-element-subtitle {
    margin-top: 0.5em;
    font-size: 1.0em;
  }
  
  .vertical-timeline-element-title {
    font-size: 0.8em;
    color: teal;
  }
}