.introduction {
  width: 100%;
  display: flex;
  margin-top: 4em;
  margin-bottom: 4em;
}

.different {
  color: teal;
}

.introduction_logocontainer {
  flex-basis: 45%;
  display: flex;
  justify-content: center;
}

.introduction_logocontainer > img {
  width: 55%;
  height: 100%;
  border-radius: 50px;
}

#user-detail-intro {
  flex-basis: 55%;
}

#user-detail-intro > h4:nth-child(1) {
  font-size: large;
  margin-top: 0;
  line-height: 26pt;
}
#user-detail-intro > h4:nth-child(1) span{
  color: teal;
}

#user-detail-intro > h4:nth-child(2) {
  margin-top: 1.4em;
  line-height: 1.4em;
}

#user-detail-intro > h4 {
  font-size: 1.1em;
  margin-top: 0.5em;
}

.icons {
  position: relative;
  top: 5px;
  color: var(--clr-fg-alt);
  margin-right: 0.5em;
}

@media (max-width: 600px) {
  .introduction {
    flex-direction: column;
    margin-top: 2em;
  }
  .introduction_logocontainer {
    margin-bottom: 3em;
    flex-basis: 30%;
  }
  .introduction_logocontainer > img {
    width: 60%;
    height: 100%;
    border-radius: 20px;
  }
  #user-detail-intro {
    padding-left: 10%;
    padding-right: 10%;
    flex-basis: 70%;
  }
}
